body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-popover-inner-content {
  background-color: rgb(28,27,37);
}
.ant-popover-message-title {
  color: white;
}

.ant-popover-buttons .ant-btn-primary {
  background-color: rgb(255,67,66);
  border: none;
}
.ant-popover-buttons .ant-btn-primary:hover {
  background-color: rgb(255,67,66);
  border: none;
}

.ant-popover-buttons .ant-btn-primary:focus {
  background-color: rgb(255,67,66);
  border: none;
}

.ant-popover-buttons button {
  background-color:rgb(60,58,75);
  border: none;
  color: white;
}

.ant-popover-buttons button:hover {
  background-color:rgb(60,58,75);
  border: none;
  color: white;
}
.ant-popover-buttons button:focus {
  background-color:rgb(60,58,75);
  border: none;
  color: white;
}
